import React from 'react';
import Sign from './Sign';

function Status(props) {
    if (props.winner) {
        return <div> Chiến thắng: <Sign player={props.winner} /> </div>;
    } else {
        return <div> Lượt tiếp theo: <Sign player={props.nextPlayer} /> </div>;
    }
}

export default Status;



