import React from 'react';
import Board  from './Board';
import Status from './Status';
import $ from 'jquery';

class Position {
    constructor(i, column) {
        this.row = Math.floor(i / column);
        this.col = (i % column);
    }

    toString() {
        return `${this.row}, ${this.col}`;
    }
}
class Game extends React.Component {

    constructor() {
        super();
        this.state = {
            column: 0,
            history: [{
                squares: Array(0).fill(null),
            }],
            nextPlayer: 'X',
            stepNumber: 0,
            isMovesSortReversed: false,

        };

        this.handleClick = this.handleClick.bind(this);
        this.jumpTo = this.jumpTo.bind(this);
    }

    currentSquares() {
        return this.state.history[this.state.stepNumber].squares;
    }



    handleClick(i) {
        const squares = this.currentSquares().slice();
        if (this.calculateWinner(squares) || squares[i]) {
            return;
        }
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        squares[i] = this.state.nextPlayer;
        this.setState({
            history: history.concat([{
                squares: squares,
                player: this.state.nextPlayer,
                position: new Position(i, this.state.column),
            }]),
            nextPlayer: (this.state.nextPlayer === 'X') ? 'O' : 'X',
            stepNumber: history.length,
        });
    }
    /* eslint-disable */
    calculateWinner(squares) {
        const lines = [];
        var BOARD_SIZE = parseInt(this.state.column)
        for(let i = 0; i < squares.length; i++)
        {
            var row = Math.floor(i / BOARD_SIZE);
            var col = (i % BOARD_SIZE);
            if(col <= (BOARD_SIZE - 5) && squares[i] && squares[i] === squares[i+1] && squares[i+1] === squares[i+2] && squares[i+2] === squares[i+3] && squares[i+3] === squares[i+4])
            {
                lines.push(i);
                lines.push(i+1);
                lines.push(i+2);
                lines.push(i+3);
                lines.push(i+4);
                return {player: squares[i], line: lines};
            }

            if(row <= (BOARD_SIZE - 5) && squares[i] && squares[i] === squares [i+(BOARD_SIZE*1)] && squares[i+(BOARD_SIZE*1)] === squares [i+(BOARD_SIZE*2)] && squares[i+(BOARD_SIZE*2)] === squares [i+(BOARD_SIZE*3)]&& squares[i+(BOARD_SIZE*3)] === squares [i+(BOARD_SIZE*4)])
            {
                lines.push(i);
                lines.push(i+(BOARD_SIZE*1));
                lines.push(i+(BOARD_SIZE*2));
                lines.push(i+(BOARD_SIZE*3));
                lines.push(i+(BOARD_SIZE*4));
                return {player: squares[i], line:lines};
            }
            if(row <= (BOARD_SIZE - 5) && col <=  (BOARD_SIZE - 5) && squares[i] && squares[i] === squares [i+(BOARD_SIZE*1+1)] && squares[i+(BOARD_SIZE*1+1)] === squares [i+(BOARD_SIZE*2+2)] && squares[i+(BOARD_SIZE*2+2)] === squares [i+(BOARD_SIZE*3+3)]&& squares[i+(BOARD_SIZE*3+3)] === squares [i+(BOARD_SIZE*4+4)])
            {
                lines.push(i);
                lines.push(i+(BOARD_SIZE*1+1));
                lines.push(i+(BOARD_SIZE*2+2));
                lines.push(i+(BOARD_SIZE*3+3));
                lines.push(i+(BOARD_SIZE*4+4));
                return {player: squares[i], line:lines};
            }
        }
        return null;
        if(row <= (BOARD_SIZE - 5) && col >= 4 && squares[i] && squares[i] === squares [i+(BOARD_SIZE*1-1)] && squares[i+(BOARD_SIZE*1-1)] === squares [i+(BOARD_SIZE*2-2)] && squares[i+(BOARD_SIZE*2-2)] === squares [i+(BOARD_SIZE*3-3)]&& squares[i+(BOARD_SIZE*3-3)] === squares [i+(BOARD_SIZE*4-4)])
        {
            lines.push(i);
            lines.push(i+(BOARD_SIZE*1-1));
            lines.push(i+(BOARD_SIZE*2-2));
            lines.push(i+(BOARD_SIZE*3-3));
            lines.push(i+(BOARD_SIZE*4-4));
            return {player: squares[i], line: lines};
        }
    }

    jumpTo(step) {
        this.setState({
            stepNumber: step,
            nextPlayer: (step % 2) ? 'O' : 'X',
        });
    }
    changeMovesSort(isMovesSortReversed) {
        this.setState({
            isMovesSortReversed: !isMovesSortReversed,
        });
    }
    changeColumn(column)
    {
        if(column < 5 || column > 17)
        {
            alert("Vui lòng nhập >=5 và <=17")
        }
        else {
            $( ".btn-circle" ).hide();
            this.setState({
                column: column,
                history: [{
                    squares: Array(column * column).fill(null),
                }],
            });
        }
    }


    render() {
        const style1={
            marginTop: '34px',
            marginBottom: '50px'
        }
        const input_column = this.state.column;
        const history = this.state.history;
        const lastSquares = history[history.length - 1].squares;
        const winner = this.calculateWinner(lastSquares);
        const winnerPlayer = winner ? winner.player : null;
        let winnerLine = null;
        // only show winner line when last move is selected in history
        if (winner && this.state.stepNumber === history.length - 1 ) {
            winnerLine = winner.line;
        }
        const moves = history.map((step, move) => {
            let desc;
            if (!move) {
                desc = 'Bắt đầu';
            } else if (move === history.length - 1 && winner) {
                desc = 'Chiến thắng ' + winnerPlayer + ': ' + step.position.toString();
            } else if (move === this.state.stepNumber) {
                desc = <b>Di chuyển {step.player}: {step.position.toString()} </b>;
            } else {
                desc = 'Di chuyển ' + step.player + ': ' + step.position.toString();
            }

            return (
                <li key={move}>
                    <a href="#" onClick={() => this.jumpTo(move)}>{desc}</a>
                </li>
            );
        });
        const { isMovesSortReversed } = this.state;
        return (
            <div className="container-fluid">
                <div className="row Font">
                    <b> GAME CARO </b>
                </div>
                <div className="row" style={style1}>

                    <div className="col-md-1">

                    </div>
                    <div className="col-md-2">
                        <form>
                            <div className="form-group">
                                <label for="nhapSoHang">Nhập số hàng và cột
                                    <input type="text" id="nhapSoHang" ref="txtHang" placeholder="Điều kiện: 5<=x<= 17"></input>
                                </label>

                                <button type="button" className="btn btn-success btn-circle" onClick={()=> this.changeColumn(this.refs.txtHang.value)}>Chơi</button>
                            </div>
                        </form>
                        <Status winner={winnerPlayer} nextPlayer={this.state.nextPlayer} />
                        <div className="game-info">
                            <ol className="scroll" reversed={isMovesSortReversed ? 'reversed' : ''}>
                                {isMovesSortReversed ? moves.reverse() : moves}
                            </ol>

                            <p><strong>Sort: </strong>{isMovesSortReversed ? 'Giảm dần' : 'Tăng dần'}</p>
                            <button onClick={() => this.changeMovesSort(isMovesSortReversed)}>
                                Toggle
                            </button>
                        </div>
                    </div>
                    <div className="col-md-6">
                        <div className="game">
                            <div className="game-board">
                                <Board input_column = {input_column} winnerLine={winnerLine} squares={this.currentSquares()}
                                       onClick={this.handleClick} />
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}
export default Game;


